，而濃談卻是固定的（墨汁廠生產墨汁的時候就已經調好了），我們書寫的時候，墨汁太濃，拉不開筆、容易滯澀；墨汁太淡，筆一上紙就洇墨，總之濃淡不合適的話都會影響到書寫。因此，墨汁濃了就要加水，淡了就適當加一些，使墨濃淡適宜，才能寫出好字來。")]),_c('p',[_vm._v("2. 不同書體建議用不同濃淡的墨汁：寫楷書，一般多用較濃的墨；而寫行草，墨汁就要相應的淡些。")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"mx-auto col-sm-12 col-md-8 col-lg-6 col-12 mx-auto col-lg-6 col-12 text-center"},[_c('h3',[_vm._v("注意項目")]),_c('p',{staticClass:"mt-5"},[_vm._v("1. 因為墨汁的濃度較大，所以在使用時可根據作品風格取出部分墨汁適量加水，但是需要注意的是加水後的墨汁千萬不要再倒入瓶內，否則會造成瓶內墨汁變質。尤其是炎熱的夏天，兌了水的墨汁更容易臭掉，這就是因為墨汁變質的緣故。")]),_c('p',[_vm._v("2. 書寫完作品後要放置24小時晾乾，讓墨固定下來，以免托裱時跑墨。")]),_c('p',[_vm._v("3. 冬季凝凍是傳統墨汁的特性，所以應在高於10℃的室內存放。如果凝凍，可以用溫水浸泡瓶身即可融化，質量如初。")]),_c('p',{staticClass:"mb-16"},[_vm._v("4. 千萬別將墨汁沾在衣服上，否則很難徹底清洗乾淨。")])])])])])])}
